import uniList from '../../components/uni-list/uni-list.vue'
import uniItem from '../../components/uni-list-item/uni-list-item.vue'
import uniPopup from '../../components/uni-popup/uni-popup.vue'

export default {
	data() {
		return {
			waitTime:'',
			avatarUrl: '',
			name: '',
			isVip: '2',
			superiorId: '',
			openId: '',
			shopStat: 2
		}
	},
	onShow() {
		uni.getSetting({
			success:res=>{
				if (res.authSetting['scope.userInfo']){
					this.getUserInfo()
				} else {
					uni.navigateTo({
						url: '../index/index-login'
					})
				}
			}
		})
		uni.getStorage({
			key: 'openId',
			success: res => {
				this.openId = res.data
				this.shopStatRequest()
			}
		})
	},
	onLoad() {
		this.getUserInfo()
	},
	methods: {
		statRequest(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopM/updateStat',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					stat: 1,
					openid: this.openId,
					opinion: ''
				},
				success: res => {
					console.log(res)
					this.closePopup()
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		onNext(e){
			if(e == 8){
				this.statRequest()
			} else if( e == 2 || e == 5){
				this.closePopup()
			} else if( e == 3){
				uni.navigateTo({
					url: 'contract'
				})
			} else if( e == 6){
				uni.navigateTo({
					url: 'upload-photo'
				})
			}
		},
		openPopup(){
			this.$refs.popup.open()
		},
		closePopup(){
			this.$refs.popup.close()
		},
		shopStatRequest(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/selectPass',
				method: 'GET',
				data: {
					openid: this.openId
				},
				success: res => {
					console.log(res.data.data)
					this.shopStat = res.data.data.stat
					if(this.shopStat == 2 || this.shopStat == 3 || this.shopStat == 5 || this.shopStat == 6 || this.shopStat == 8){
						this.openPopup()
					} return
				},
				fail: err => {
					console.log(err)
				}
			});
		},
		getUserInfo(){
			uni.getUserInfo({
				success:res=>{
					this.avatarUrl = res.userInfo.avatarUrl
					this.name = res.userInfo.nickName
				}
			})
		},
		onBeVip() {
			uni.navigateTo({
				url: '../bevip/bevip',
				success: res => {
					console.log('success')
				}
			});
		},
		onOrderList() {
			uni.navigateTo({
				url: '../orderlist/orderlist'
			});
		},
		onMyWallet() {
			uni.navigateTo({
				url: '../mywallet/mywallet'
			});
		},
		onAddressList() {
			uni.navigateTo({
				url: '../addresslist/addresslist'
			});
		},
		onBusinessIn() {
			if(this.shopStat == 7){
				uni.navigateTo({
					url: 'business-in'
				})
			} else {
				uni.navigateTo({
					url: 'shop-stat?id=' + this.shopStat
				})
			}
		},
		onFriends() {
			uni.navigateTo({
				url: 'my-friends'
			})
		},
		onCards() {
			uni.navigateTo({
				url: 'cards'
			})
		},
		onlineKf() {
			uni.navigateTo({
				url: 'online-kf'
			})
		}
	},
	components: {
		uniList,
		uniItem,
		uniPopup
	},
}
